<script>
  import LoginBasic from '../../components/LoginBasic'

  export default {
    mounted() {
      this.offsetTop = this.$refs.fixedBlock.getBoundingClientRect().top;
      // console.log('---->', this.$refs.fixedBlock.getBoundingClientRect());
      window.addEventListener('scroll', this.handleScroll);
      this.$once('hook:beforeDestroy', () => {
        window.removeEventListener('scroll', this.handleScroll);
      });
      window.addEventListener("beforeunload", () => {
        window.scrollTo(0, 0);
      });
    },
    methods: {
      handleScroll() {
        // console.log('handleScroll', this.$refs.fixedBlock.getBoundingClientRect());
        const scrollTop = document.body.scrollTop || document.documentElement.scrollTop || window.pageYOffset;
        // const offsetTop = document.querySelector('#fixedId').offsetTop
        // console.log('handleScroll', scrollTop, this.offsetTop);
        if (scrollTop > this.offsetTop) {
          this.isFixed = true;
          this.scrollTop = scrollTop
        } else {
          this.isFixed = false
        }
      },
    },
    computed: {
      // 计算属性的 getter
      getFixedStyle() {
        if (this.isFixed) {
          let top = this.scrollTop - this.offsetTop;
          return 'top: ' + top + 'px;'
        } else {
          return {}
        }
      }
    },
    data() {
      return {
        isFixed: false,
        offsetTop: 0,
        scrollTop: 0,
      }
    },
    components: {LoginBasic},
    name: "AsideBasic",
  }
</script>

